<template>
  <div>
    <CRow>
      <CCol sm="6" lg="6">
        <CCard>
          <CCardHeader>{{ date }} ({{ $t('billing.totalCost') }})</CCardHeader>
          <CCardBody class="cardCost">$0.00</CCardBody>
          <!-- <CCardFooter>3</CCardFooter> -->
        </CCard>
      </CCol>
      <CCol sm="6" lg="6">
        <CCard>
          <CCardHeader>{{ yearMonth }} ({{ $t('billing.forecastTotalCost') }})</CCardHeader>
          <CCardBody class="cardCost">$0.00</CCardBody>
          <!-- <CCardFooter>3</CCardFooter> -->
        </CCard>
      </CCol>
    </CRow>
    <CCard>
      <CCardBody>
        <CRow>
          <CCol>
            <CSelect :label="$t('billing.project')" :options="option_project" class="projectSelect" />
          </CCol>
          <CCol>
            <CSelect :label="$t('billing.month')" :options="option" class="projectSelect" />
          </CCol>
          <CCol>
            <CButton block variant="outline" color="dark" class="dash_downloadButton">
              <CIcon name="cil-cloud-download" />
            </CButton>
          </CCol>
        </CRow>
      </CCardBody>
    </CCard>
    <CCard>
      <CCardBody>
        <CRow>
          <CCol sm="4" class="costComparison">
            <div class="priceFont">$100.00</div>
            <div>
              (上個月)
            </div>
          </CCol>
          <CCol sm="4" class="costComparison">
            <div class="priceFont">
              $200.00
            </div>
            <div>
              (這個月)
            </div>
          </CCol>
          <CCol sm="4" class="costComparison">
            <div class="priceFont redFont">
              $100.00
            </div>
            <div>
              (差值)
            </div>
          </CCol>
        </CRow>
      </CCardBody>
    </CCard>
    <CCard>
      <CCardBody>
        <CRow>
          <CCol sm="5">
            <span class="titleFont ">總用量</span>
            <div class="small text-muted mb-3">{{ getMonth }}</div>
          </CCol>
        </CRow>
        <MonthlyChartLine />
      </CCardBody>
    </CCard>
    <CCard>
      <CCardBody>
        <CRow>
          <CCol sm="5">
            <span class="titleFont ">各服務用量</span>
            <div class="small text-muted mb-3">{{ getMonth }}</div>
          </CCol>
        </CRow>
        <MonthlyChartBar />
      </CCardBody>
    </CCard>

    <!-- table -->
    <CCard>
      <CCardBody>
        <!-- column-filter -->
        <CDataTable :items="items" :fields="fields" table-filter items-per-page-select :items-per-page="5" hover sorter pagination> </CDataTable>
      </CCardBody>
    </CCard>
  </div>
</template>
<script>
import MonthlyChartLine from '../charts/MonthlyChartLine';
import MonthlyChartBar from '../charts/MonthlyChartBar';
// import Bus from '../../bus.js';

export default {
  name: 'MonthlyBilling',
  components: {
    MonthlyChartLine,
    MonthlyChartBar,
  },
  data() {
    return {
      getMonth: '',
      items: [
        { service: 'Cloud Storage', cost: '$ 0.05', subtotal: '$0.00' },
        { service: 'Compute Engine', cost: '$ 645.86', subtotal: '$0.00' },
        { service: 'APP Engine', cost: '$ 0.00', subtotal: '$0.00' },
        { service: 'Stackdriver Logging', cost: '$ 0.1', subtotal: '$0.00' },
        { service: 'Load Balancer', cost: '$ 0.05', subtotal: '$0.00' },
        { service: 'Cloud Build', cost: '$ 0.00', subtotal: '$0.00' },
        { service: 'BigQuery', cost: '$ 645.86', subtotal: '$0.00' },
        { service: 'Cloud SQL', cost: '$ 0.1', subtotal: '$0.00' },
        { service: 'Cloud DNS', cost: '$ 0.05', subtotal: '$0.00' },
        { service: 'Cloud Dataflow', cost: '$ 0.15', subtotal: '$0.00' },
        { service: 'Other', cost: '$ 0.00', subtotal: '$0.00' },
      ],
      details: [],
      collapseDuration: 0,
      option: [4, 3, 2, 1],
      option_project: ['專案A', '專案B', '專案C'],
      date: '',
      yearMonth: '',
    };
  },
  computed: {
    fields() {
      return [
        {
          key: 'service',
          label: this.$t('billing.service'),
          sortable: true,
        },
        {
          key: 'cost',
          label: this.$t('billing.cost'),
          sortable: false,
        },
        {
          key: 'subtotal',
          label: this.$t('billing.subtotal'),
          sortable: true,
          variant: 'danger',
        },
      ];
    },
  },
  mounted() {
    // 日期區間 utils/common.js共用
    this.date = this.common.getDateRange();
    this.yearMonth = this.common.getMonth();
    this.getMonth = this.common.getChartMonth();
  },
  methods: {
    toggleDetails(item) {
      this.$set(this.items[item.id], '_toggled', !item._toggled);
      this.collapseDuration = 300;
      this.$nextTick(() => {
        this.collapseDuration = 0;
      });
    },
  },
};
</script>
